// Images like a BG
.ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  img {
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden; } }
//-----end

// Custom link style

.link {
  text-decoration: none;
  font-size: 16px;
  font-weight: 700;
  line-height: em(25px, 16px);
  color: #D11586;
  letter-spacing: 0.16em;
  position: relative;
  display: inline-block;
  &:after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #D11586; }

  &_2 {
    letter-spacing: normal;
    color: #ffffff;
    &:after {
      display: none; } } }


